  grid-template-columns: repeat(5, 1fr);
  gap: 1rem;
  align-items: center;
}
.contenedor1 .three.columns {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.contenedor1 label {
  align-self: flex-start;
  margin-bottom: 0.5rem;
  font-weight: bold;
  color: #FFFFFF;
}
.contenedor1 .input-container {
  width: 100%;
  display: flex;
  justify-content: center;
}
.contenedor1 input,
.contenedor1 select {
  width: 100%;
  padding: 0.5rem;
  background-color: #1a1a1a;
  border: 1px solid #ccc;
  border-radius: 4px;
  color: #FFFFFF;
}

.oculto {
  display: none;
  opacity: 0;
}

.mobile-menu1 {
  display: flex;
  /* Establece flexbox en el contenedor */
  justify-content: center;
  /* Centra el contenido horizontalmente */
  align-items: center;
  height: 3rem;
}
.mobile-menu1 img {
  width: 3rem;
  cursor: pointer;
}

.contenedor2 form {
  max-width: 1100px;
  margin: 0 auto;
  padding: 4rem;
  background: #1a1a1a;
  margin-bottom: 4rem;
  font-family: Arial, Helvetica, sans-serif;
  opacity: 1;
  transition: opacity 0.5s ease;
}
.contenedor2 form legend {
  color: #FFFFFF;
  font-size: 2rem;
}
