.acciones-auth {
  display: flex;
  justify-content: space-between;
}
.acciones-auth a {
  color: #FFFFFF;
  margin-top: 1.5rem;
}

.eventos1__heading {
  color: #007df4;
  background: linear-gradient(to right, #9966FF, #990066);
  /* Degradado de color */
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin: 2rem 0;
  text-align: left;
  font-size: clamp(2rem, -0.8rem + 8vw, 6rem);
  font-weight: bold;
}

